    padding-left: 24px;
    text-align: center;
    float: none; } }

.page-content p {
  color: #fff;
  letter-spacing: 2px;
  word-spacing: 2px; }

.page-content img {
  padding-top: 14px;
  max-width: -webkit-fill-available;
  max-height: 800px; }
  @media (max-width: 414px) {
    .page-content img {
      max-height: 600px; } }

@media (max-width: 414px) {
  .list .col-md-6 {
    padding-left: 0px;
    padding-right: 0px; } }

.list-content {
  padding: 30px; }
  .list-content h3, .list-content p {
    color: #fff;
    text-shadow: 1px 1px 5px #000;
    font-weight: bold;
    letter-spacing: 3px; }

.experience {
  height: 400px; }
  @media (max-width: 414px) {
    .experience {
      height: 150px; } }
  .experience h1 {
