.wwd-content h3 {
  margin-top: 0px;
  padding-top: 4em;
  font-family: AlfaSlabOne;
  letter-spacing: 3px;
  text-align: center;
  font-size: 30px;
  text-transform: uppercase;
  text-shadow: 1px 1px 5px #000;
  color: #fff;
  font-weight: bold; }

.wwd-content p {
  padding: 0 10px;
