  letter-spacing: 2px;
  font-weight: bold;
  text-shadow: 1px 1px 5px #000;
  color: #fff; }

.contact-heading h2 {
  text-align: center;
  color: #fff;
  letter-spacing: 3px; }

.contact-form {
  padding: 0 20px; }

.fp-col-right h2 {
  text-align: center;
  color: #fff;
  letter-spacing: 3px; }

.fp-col-right p {
  color: #fff;
  letter-spacing: 1px; }

.fp-col-right img {
  max-width: -webkit-fill-available; }

.slick-image-slide-wrap img {
