.portfolio-page-header h1 {
  text-align: center;
  margin-top: 1em;
  margin-bottom: 1em; }

.portfolio-title h2 {
  text-align: center; }

.web-content {
  padding-bottom: 1em; }

@media (min-width: 320px) {
