    text-align: center; }
    .portfolio-img img {
      width: 100%; } }

.content-title h2 {
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
  letter-spacing: 3px;
  font-weight: bold;
  font-family: AlfaSlabOne;
  color: #fff; }

.custom-page-content {
  padding-bottom: 20px; }
  .custom-page-content img {
    width: 100%;
